import { createApp } from 'vue';
import App from './App.vue';
import ElementPlus from 'element-plus';
import router from '@/router'; //引入路由配置
import 'element-plus/dist/index.css'; // 引入 Element Plus 样式
import '@/assets/styles/css/reset.css';   // 引入重置样式
import '@/assets/styles/css/base.css';    // 引入基础样式
import '@/assets/styles/css/layout.css';  // 引入布局样式
import '@/assets/styles/css/typography.css'; // 引入排版样式
import '@/assets/styles/css/variables.css';  // 引入 CSS 变量
import '@/assets/styles/css/menu.css';  // 引入 CSS 变量
const app = createApp(App);
app.use(ElementPlus);
app.use(router);
app.mount('#app');