<template>
  <!-- <div class="common-layout">
    <el-image
      v-for="url in urls"
      :key="url"
      :src="url"
      lazy
    />
  </div> -->
  <div>
    <div class="carousel-container">
      <!-- 轮播图 -->
      <div
        class="carousel-wrapper"
        :style="carouselStyle">
        <div
          v-for="(item, index) in images"
          :key="index"
          class="carousel-item">
          <img
            :src="item.src">
        </div>
      </div>

      <!-- 左右箭头 -->
      <button
        class="prev"
        @click="prevSlide">
        ‹
      </button>
      <button
        class="next"
        @click="nextSlide">
        ›
      </button>
    </div>
    <el-row :span="24">
      <el-col :span="24">
        <el-card>
          <h3>Card 1</h3>
          <p>Some content for Card 1</p>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import image1 from '@/assets/images/test1.png';
import image2 from '@/assets/images/test2.png';
import image3 from '@/assets/images/test3.png';
import image4 from '@/assets/images/test4.png';
import image5 from '@/assets/images/test5.png';
export default {
  name: 'MyHome',

  data() {
    return {
      // 将 urls 数组放入 data 中，确保它能在组件内访问
      urls: [
        require('@/assets/images/test1.png')
      ],
      // 轮播图的图片列表
      images: [
        { src: image1},
        { src: image2},
        { src: image3,},
        { src: image4,},
        { src: image5,},
      ],
      currentIndex: 0,  // 当前显示的图片索引
      timer: null,       // 定时器，用于自动轮播
    };
  },
  computed: {
    // 动态计算轮播图的 CSS 样式
    carouselStyle() {
      const offset = -this.currentIndex * 100;  // 计算偏移量，单位是百分比
      return {
        transform: `translateX(${offset}%)`,
        transition: 'transform 0.5s ease-in-out',  // 添加平滑的过渡动画
      };
    }
  },
  beforeUnmount() {
    // 组件销毁时清除定时器
    this.stopAutoSlide();
  },
  mounted() {
    // 启动自动轮播
    this.startAutoSlide();
  },
  methods: {
    // 手动切换到上一张
    prevSlide() {
      this.currentIndex = (this.currentIndex - 1 + this.images.length) % this.images.length;
    },
    // 手动切换到下一张
    nextSlide() {
      this.currentIndex = (this.currentIndex + 1) % this.images.length;
    },
    // 自动切换到下一张
    startAutoSlide() {
      this.timer = setInterval(this.nextSlide, 3000); // 每3秒切换一次
    },
    // 停止自动轮播
    stopAutoSlide() {
      clearInterval(this.timer);
    }
  }
};
</script>

<style scoped>
.carousel-container {
  position: relative;
  width: 100%;
  overflow: hidden;
  /* 隐藏溢出的部分 */
}

.carousel-wrapper {
  display: flex;
  transition: transform 0.5s ease-in-out;
}

.carousel-item {
  min-width: 100%;
  height: 600px;
  object-fit: cover;
}

.carousel-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  padding: 10px;
  font-size: 24px;
  cursor: pointer;
}

.prev {
  left: 10px;
}

.next {
  right: 10px;
}
</style>
