<template>
  <div class="common-layout">
    <el-container>
      <el-header>
        <el-menu
          class="menu"
          :default-active="activeIndex"
          mode="horizontal"
          @select="handleSelect"
          style="font-size: 18px;
          justify-content: space-between;"
        >
          <el-menu-item
            v-for="route in routes"
            :key="route.path"
            :index="route.path"
          >
            {{ route.name }}
          </el-menu-item>
        </el-menu>
      </el-header>
      <el-main>
        <router-view />
      </el-main>
      <el-footer>
        <Bottom :ref="'Bottom'" />
      </el-footer>
    </el-container>
  </div>
</template>

<script>
import { useRouter, useRoute } from 'vue-router';
import Bottom from '@/components/bottom.vue';

export default {
  name: 'MyHome',components: {
    Bottom, // 注册 Bottom 组件
  },
  setup() {
    const router = useRouter();// 获取路由实例
    const route = useRoute();
    const routes = [
      { path: '/home', name: '首页' },
      { path: '/products', name: '产品中心' },
      { path: '/about', name: '关于我' },
      { path: '/bookMark', name: '收藏' },
      { path: '/callUs', name: '联系我们' }
      // 可以继续添加更多路由
    ];
    const handleSelect = (path) => {
      router.push(path); // 路由跳转
    };

    const activeIndex = route.path; // 当前路由路径作为默认激活项

    return {
      routes,
      handleSelect,
      activeIndex,
    };
  },
};
</script>

